.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }

  p {
    color: red;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
  }
}
